.burger {
    background: var(--blue);
    box-shadow: 1px 1px 1px var(--black);
    margin: .4em;
    position: relative;
    height: 4px;
    width: 25px;
    transition: all .5s ease-in-out;
}

.burger:before {
    background: var(--white);
    box-shadow: 1px 1px 1px var(--black);
    content: '';
    position: absolute;
    top: -8px;
    height: 4px;
    width: 25px;
    transition: all .5s ease-in-out;
}

.burger:after {
    background: var(--yellow);
    box-shadow: 1px 1px 1px var(--black);
    content: '';
    position: absolute;
    top: 8px;
    height: 4px;
    width: 25px;
    transition: all .5s ease-in-out;
}

.burger.active {
    -webkit-transform: scale(2) rotate(135deg);
            transform: scale(2) rotate(135deg);
    transition: all .5s ease-in-out;
}

.burger.active:before {
    opacity: 0;
    transition: all .5s ease-in-out;
}

.burger.active:after {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    transition: all .5s ease-in-out;
    top: 0;
}

.menu {
    opacity: 0;
    transition: all .5s ease-in-out;
    width: 120px;
}

.menu.active {
    box-shadow: -3px 0 8px black;
    color: var(--white);
    opacity: 1;
    position: absolute;
    transition: all .5s ease-in-out;
    width: 450px;
}

@media (max-width: 1000px) {
    .menu.active {
        width: 100vw;
    }
}

:root {
    --black: rgb(29, 32, 41);
    --white: rgb(245, 245, 245);
    --blue: rgb(0, 135, 255);
    --yellow: rgb(249, 205, 70);
}

body {
    background: rgb(29, 32, 41);
    background: var(--black);
}

a {
    color: inherit;
}

a:visited {
    text-decoration: none;
}
